<template>
<v-touch v-on:swipeleft="onSwipeLeft"  v-on:swiperight="onSwipeRight">
  <div>
    <span class="updated" v-if="classements.updated">MAJ:{{classements.updated.substring(11, 13)}}h{{classements.updated.substring(14, 16)}}</span>
    <br>
    <div class="classements" style="width:100%;">
      <div class="classement-item" v-if="ligue === 'ligue1' && classement1s !==null" v-for="classement in classement1s">
        <div class="position" style="width:5%;display:table-cell;">
          {{classement.position}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.slug)" class="logo_classement">
        </div>
        <div style="width:30%;display:table-cell;text-align:center; vertical-align:middle;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;font-size: calc(12px + 0.6vw + 0.4vh);">
          {{classement.points}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.joues}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.diff}}
        </div>
      </div>

      <div class="classement-item" v-if="ligue === 'ligue2' && classement2s !==null" v-for="classement in classement2s">
        <div class="position" style="width:5%;display:table-cell;">
          {{classement.position}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.slug)" class="logo_classement">
        </div>
        <div style="width:30%;display:table-cell;text-align:center; vertical-align:middle;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;font-size: calc(12px + 0.6vw + 0.4vh);">
          {{classement.points}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.joues}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.diff}}
        </div>
      </div>
    </div>
  </div>
</v-touch>
</template>

<script>
var debounce = require('lodash.debounce')
export default {
  name: 'classements',

  data () {
    return {
      classements: '',
      classement1s: [],
      classement2s: [],
      classement: '',
      ligue: ''
    }
  },
  beforeRouteUpdate (to) {
    this.ligue = this.$store.state.ligue
  },
  methods: {
    onSwipeRight () {
      if (this.ligue === 'ligue1') {
        this.$router.push('/')
      } else if (this.ligue === 'ligue2') {
        // this.$store.commit('setLigue', 'ligue1')
        this.$localStorage.set('ligue', 'ligue1')
        this.$router.push('/ligue1')
      }
    },
    onSwipeLeft () {
      if (this.ligue === 'ligue1') {
        // this.$store.commit('setLigue', 'ligue2')
        this.$localStorage.set('ligue', 'ligue2')
        this.$router.push('/ligue2')
      } else if (this.ligue === 'ligue2') {
        this.$router.push('/')
      }
    },
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    update () {
      this.ligue = this.$store.state.ligue
      this.ligue = this.$localStorage.get('ligue')
      this.classements = this.$store.state.classements
      this.classement1s = this.classements.ligue1.classement
      this.classement2s = this.classements.ligue2.classement
    },
    updateClassements: debounce(function () {
      this.update()
    }, 1000)
  },

  watch: {
    classements: function () {
      this.updateClassements()
    },
    '$route' (to, from) {
      this.update()
    }
  },

  created: function () {
    window.addEventListener('online', this.updateClassements)
  },

  mounted: function () {
    this.update()
  }
}
</script>

<style>
.classements{
  font-size: calc(10px + 0.6vw + 0.4vh);
  font-weight: 600;
}
.classement-head{
  text-align: center;
  vertical-align: middle;
  min-height: 10px;
}
.classement-item{
  /*border-bottom: 1px solid;*/
  text-align: center;
  vertical-align: middle;
  min-height: 30px;
  margin-bottom:2px;
}
.updated{
  float: right;
  position: relative;
  bottom: 20px;
}
.logo_classement{
  height: calc(20px + 6vw);
  width: calc(20px + 6vw);
  padding: 0;
  max-width: 80px;
  max-height: 80px;
}
.classement-item:nth-child(odd){
  background-color: #E0E0E0;
}
.classement-item:nth-child(odd)>.position{
  background-color:#CFD8DC;
}
</style>



